import * as Contract from '@tableau/api-external-contract-js';
import { DashboardObjectImpl } from '../Impl/DashboardObjectImpl';
import { ApiService } from './ServiceRegistry';

/**
 * API Service for interacting with Dashboard Zones
 */
export interface ZoneService extends ApiService {
  /**
   * Set the Visibility of a list of dashboard zones.
   *
   * @param {String} dashboard The name of the dashboard
   * @param {Contract.ZoneVisibilityMap} zoneVisibilityMap A map of ZoneIds to the visibility value of the zone
   * @returns {Promise<void>} Promise which resolves to void.
   *
   * @Since 1.1.0
   */
  setVisibilityAsync(
    dashboard: string,
    dashboardZoneMap: Map<number, DashboardObjectImpl>,
    zoneVisibilityMap: Contract.ZoneVisibilityMap,
  ): Promise<void>;

  moveAndResizeAsync(
    dashboard: string,
    dashboardZoneMap: Map<number, DashboardObjectImpl>,
    dashboardObjectPositionAndSizeUpdateArray: Contract.DashboardObjectPositionAndSizeUpdateArray,
  ): Promise<void>;
}
